
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.container {
  background: #66677c;
}
.card {
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  top: 0;
}
.card:hover {
    top: -3px;
}
.card .cover {
    padding: 10px 10px 0 10px;
    line-height: 0;
}
.card .cover img {
      width: 100%;
}
.card .name {
    background: #fff;
    color: #dd4b39;
    font-weight: 600;
    padding: 10px 20px;
    font-size: 18px;
}
.card .title {
    background: #fff;
    color: #666;
    padding: 10px 20px;
    font-size: 14px;
}
.menu-wrapper {
  padding: 20px;
}
.menu-wrapper .menu {
    display: block;
    width: 180px;
    height: 40px;
    border-radius: 3px;
    background: #fff;
    border: none;
    outline: none;
    color: #666;
    margin: 0 auto;
    cursor: pointer;
}
.flashcard {
  cursor: pointer;
  border-radius: 10px;
  margin: 10px;
  padding: 15px;
  text-align: center;
}
.flashcard:hover {
  -webkit-box-shadow: 0 0px 25px rgba(0, 0, 0, 0.8);
          box-shadow: 0 0px 25px rgba(0, 0, 0, 0.8);
}
